<template>
	<el-row class="container">
		<el-col :span="24" class="header">
			<el-col :span="10" class="logo" :class="isCollapse?'logo-collapse-width':'logo-width'">
				<i class="el-icon-mc-shezhi"></i> {{isCollapse?'':sysName}}
			</el-col>
			<el-col :span="10">
				<div class="tools" @click.prevent="collapse">
					<i class="el-icon-mc-quanbu" title="展开/收起导航"></i>
				</div>
			</el-col>
			<el-col :span="4" class="userinfo">
				<el-dropdown trigger="hover">
					<span class="el-dropdown-link userinfo-inner"><img :src="this.sysUserAvatar" /> {{sysUserName}}</span>
					<el-dropdown-menu slot="dropdown">
						<!-- <el-dropdown-item>我的消息</el-dropdown-item>
						<el-dropdown-item>设置</el-dropdown-item> -->
						<el-dropdown-item @click.native="$router.push({path: '/password'})">修改密码</el-dropdown-item>
						<el-dropdown-item @click.native="logout">退出登录</el-dropdown-item>
					</el-dropdown-menu>
				</el-dropdown>
			</el-col>
		</el-col>
		<el-col :span="24" class="main">
			<!--导航菜单-->
			<el-menu :default-active="$route.path" class="el-menu-vertical-demo" @open="handleopen" @close="handleclose" @select="handleselect"
				 unique-opened router :collapse="isCollapse" background-color="#eef1f6">
				<template v-for="(item,index) in $router.options.routes" v-if="!item.hidden">
					<el-submenu :index="index+''" v-if="!item.leaf" :key="item.index">
						<template slot="title"><i :class="item.iconCls"></i><span slot="title">{{item.name}}</span></template>
						<el-menu-item
							v-for="child in item.children"
							:index="child.path"
							:key="child.path"
							v-if="!child.hidden">{{child.name}}</el-menu-item>
					</el-submenu>
					<el-menu-item
						v-if="item.leaf&&item.children.length>0"
						:index="item.children[0].path" :key="item.index">
						<i :class="item.iconCls"></i>
						<span slot="title">{{item.children[0].name}}</span>
					</el-menu-item>
				</template>
			</el-menu>

			<section class="content-container">
				<div class="grid-content bg-purple-light">
					<el-col :span="24" class="breadcrumb-container">
						<strong class="title">{{$route.name}}</strong>
						<el-breadcrumb separator="/" class="breadcrumb-inner">
							<el-breadcrumb-item v-for="item in $route.matched" :key="item.path">
								{{ item.name }}
							</el-breadcrumb-item>
						</el-breadcrumb>
					</el-col>
					<el-col :span="24" class="content-wrapper">
						<transition name="fade" mode="out-in">
							<router-view></router-view>
						</transition>
					</el-col>
				</div>

			</section>

		</el-col>
	</el-row>
</template>

<script>
	import { requestLoginOut } from '@/api/api';
	export default {
		data() {
			return {
				sysName:'二网公司运营管理后台',
				isCollapse: false,
				sysUserName: '',
				sysUserAvatar: '',
				form: {
					name: '',
					region: '',
					date1: '',
					date2: '',
					delivery: false,
					type: [],
					resource: '',
					desc: ''
				}
			}
		},
		methods: {
			handleopen() {
				//console.log('handleopen');
			},
			handleclose() {
				//console.log('handleclose');
			},
			handleselect: function (a, b) {
			},
			reload:function(){
	　　　　　　this.$router.go(0);  
	　　　　},
			//退出登录
			logout: function () {
				this.$confirm('确认退出吗?', '提示', {
					//type: 'warning'
				}).then(() => {
					requestLoginOut({}).then(res => {
						localStorage.removeItem('user');
						this.$router.push('/login');
					});
					
				}).catch(() => {

				});
			},
			//折叠导航栏
			collapse:function(){
				this.isCollapse=!this.isCollapse;
			},
			showMenu(i,status){
				this.$refs.menuCollapsed.getElementsByClassName('submenu-hook-'+i)[0].style.display=status?'block':'none';
			}
		},
		mounted() {
			let user = localStorage.getItem('user');
			if (user) {
				user = JSON.parse(user);
				this.sysUserName = user.admin_username || '';
				this.sysUserAvatar = user.admin_img || '';
				this.sysUserId = user.admin_userid || '';
			}

		}
	}
</script>

<style scoped lang="scss">
	$color-primary: #18202a;
	.el-menu-vertical-demo:not(.el-menu--collapse) {
		width: 229px;
	}
	.container {
		position: absolute;
		top: 0px;
		bottom: 0px;
		width: 100%;
		.header {
			height: 60px;
			line-height: 60px;
			background:  #2C3E50;
			color:#fff;
			// z-index:100000;
			position: relative;
			.userinfo {
				text-align: right;
				padding-right: 35px;
				float: right;
				.userinfo-inner {
					cursor: pointer;
					color:#fff;
					img {
						width: 40px;
						height: 40px;
						border-radius: 20px;
						margin: 10px 0px 10px 10px;
						float: right;
					}
				}
			}
			.logo {
				//width:230px;
				height:60px;
				font-size: 16px;
				padding-left:20px;
				padding-right:20px;
				border-color: rgba(238,241,146,0.3);
				border-right-width: 1px;
				border-right-style: solid;
				img {
					width: 40px;
					float: left;
					margin: 10px 10px 10px 18px;
				}
				.txt {
					color:#fff;
				}
			}
			.logo-width{
				width:230px;
			}
			.logo-collapse-width{
				width:65px
			}
			.tools{
				padding: 0px 23px;
				width:14px;
				height: 60px;
				line-height: 60px;
				cursor: pointer;
			}
		}
		.main {
			display: flex;
			// background: #324057;
			position: absolute;
			top: 60px;
			bottom: 0px;
			overflow: hidden;
			.content-container {
				// background: #f1f2f7;
				flex:1;
				// position: absolute;
				// right: 0px;
				// top: 0px;
				// bottom: 0px;
				// left: 230px;
				overflow-y: scroll;
				padding: 20px;
				.breadcrumb-container {
					//margin-bottom: 15px;
					.title {
						width: 200px;
						float: left;
						color: #475669;
					}
					.breadcrumb-inner {
						float: right;
					}
				}
				.content-wrapper {
					border-top: 1px solid #f1f1f1;
					padding-top: 15px;
					background-color: #fff;
					box-sizing: border-box;
				}
			}
		}
	}
	// 面包屑
	.breadcrumb-container {
		padding-bottom: 20px;
	}
	// nav
	.el-submenu [class^=el-icon-] {
		color: $color-primary;
	}

</style>


